body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  /* this makes sure the padding and the border is included in the box sizing */
  margin: 0;
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}

::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}

::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

body {
  height: 100%;
  width: 100%;
  background-attachment: fixed;
  background-color: #000108;
  background-image: linear-gradient(#000000, rgb(4, 9, 31) 70%, #061d54);
}

.container {
  height: 100%;
  width: 100%;
  padding-top: 4rem;
  padding-bottom: 16rem;
  align-items: center;
  justify-content: center;
}

.capslock {
  color: rgb(189, 87, 83);
  font-size: small;
  margin: auto;
  padding: 1rem;
  width: 750px;
  font-family: "Source Code Pro", monospace;
}

.right.character {
  /* background-color: rgb(127, 255, 195); */
  color: #cee2ff;
  transition: all 0.15s linear;
}

.caret {
  position: absolute;
  height: 32px;
  width: 2.3px;
  background-color: white;
  transition: 130ms ease;
  line-height: 2.5rem;
  -webkit-transform: translateX(-3.5px);
          transform: translateX(-3.5px);
}

.wrong.character {
  background-color: rgb(255, 102, 128);
  transition: all 0.15s linear;
}

#missed-characters {
  color: rgb(255, 102, 128);
}

.character {
  /* padding: 0 0.03rem; */
  font-size: 1.4rem;
  font-family: "Source Code Pro", monospace;
  color: rgba(20, 49, 138, 0.767);
  white-space: break-spaces;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.big.character {
  font-size: 1.6rem;
}

.ThockeyLogo {
  color: #2654b8;
  width: 50%;
  margin: auto;
  padding: 5rem 0 5rem 0;
  text-align: center;
  font-size: 4rem;
  font-family: "Pacifico", cursive;
}

.ThockeyLogo.finished {
  font-size: 3rem;
  padding: 2rem 0 2rem 0;
}

.game_container {
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: center;
}

.background {
  position: fixed;
  z-index: -1;
}

.button {
  display: flex;
  position: relative;
  font-weight: 400;
  font-size: 0.85rem;
  height: 1.6rem;
  width: 1.6rem;
  align-items: center;
  justify-content: center;
  text-align: center;
  align-self: center;
  background-color: #1324b810;
  border: 1px solid #060f66;
  cursor: pointer;
  outline: 0;
  margin: 0.5rem;
  transition: all 200ms linear;
  color: #1324b8;
}

.button.selected {
  color: #f5ef7a;
  border: 1px solid #53531f;
}

.button:hover {
  background-color: #0c225e;
  transition: 100ms ease;
}

.button.selected:hover {
  background-color: #3b4907;
  transition: 100ms ease;
}

.words-container {
  background-color: #1324b810;
  margin: 0.5rem 0rem;
  padding: 2rem;
  border-radius: 0.5rem;
  text-align: left;
  line-height: 2.5rem;
  border: 1px solid rgb(1, 7, 70);
  overflow: hidden;
  transition: all 100ms ease-in;
}

.blink {
  -webkit-animation: blinker 0.8s linear infinite;
          animation: blinker 0.8s linear infinite;
}

@-webkit-keyframes blinker {
  33% {
    background-color: rgb(122, 150, 201);
  }
}

@keyframes blinker {
  33% {
    background-color: rgb(122, 150, 201);
  }
}

.fact-prefix {
  font-family: "Source Code Pro", monospace;
  color: #cee2ff;
}

.quote-wrapper {
  color: #cee2ff;
  font-size: 2rem;
}

.author {
  font-weight: bolder;
  font-size: 1.5rem;
  float: right;
  color: #1324b8;
}

.header {
  display: flex;
  flex: 1 1;
  margin: 0 0.5rem;
}

.subheader {
  font-weight: 500;
}

.game-container {
  display: flex;
  flex-direction: row;
  margin: 2rem 0rem 10rem 0rem;
}

.content-container {
  flex: 1 1;
  justify-content: flex-end;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.words-content-container {
  width: 850px;
}

@media only screen and (max-width: 1000px) {
  .words-content-container {
    flex: 10 1;
  }
}

.restart.button {
  display: inline-flex;
  margin-bottom: 0rem;
  margin-top: 0rem;
}

.button-container {
  margin-top: 2rem;
  float: right;
  transition: all 200ms ease;
}

#reset-label {
  margin-top: 75px;
  color: #1324b8;
  display: flex;
  flex: 1 1;
  flex-direction: row;
  font-size: 0.75rem;
  font-weight: 600;
}

#reset-label a {
  text-align: center;
  color: inherit;
  text-decoration: none; /* no underline */
}

#reset-label #key {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 0rem 0.5rem;
  padding: 0px 5px;
  border: 1px solid #1324b8;
  border-radius: 3px;
  height: 1.4rem;
}

.results {
  color: rgb(227, 237, 255);
}

.results_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.results_section {
  display: flex;
  flex: 1 1;
  justify-content: space-evenly;
  align-items: center;
}

.results_sub_section {
  display: flex;
  flex: 1 1;
  padding: 0 1rem;
}

ul {
  padding: 0;
}

#results_list li {
  margin: 0rem 1rem;
  padding: 0.25rem 1.5rem;
  display: flex;
  justify-content: space-between;
  border-radius: 4px;
}

#results_list li:nth-child(odd) {
  background-color: #1323b810;
}

.fade-out {
  opacity: 0;
  transition: all 50ms ease;
}

.fade-in {
  -webkit-animation: fadeInAnimation 150ms ease;
          animation: fadeInAnimation 150ms ease;
}

.author-fade-in {
  -webkit-animation: fadeInAnimation 300ms ease-in;
          animation: fadeInAnimation 300ms ease-in;
}

@-webkit-keyframes fadeInAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#select {
  float: right;
}

.emoji {
  margin: 0px 5px;
  -webkit-filter: saturate(0) brightness(0.4);
          filter: saturate(0) brightness(0.4);
  cursor: pointer;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.emoji.selected {
  margin: 0px 5px;
  -webkit-filter: saturate(2) brightness(1);
          filter: saturate(2) brightness(1);
  font-size: 1.5rem;
}

@media only screen and (max-width: 600px) {
  .ThockeyLogo {
    color: #2654b8;
    width: 50%;
    margin: auto;
    padding: 3rem 0 3rem 0;
    text-align: center;
    font-size: 3rem;
    font-family: "Pacifico", cursive;
  }

  .words-content-container {
    width: auto;
    margin: 0rem 2rem;
  }

  .words-container {
    background-color: #1324b810;
    margin: 0.5rem 0rem;
    padding: 2rem;
    border-radius: 0.5rem;
    text-align: left;
    line-height: 2.5rem;
    border: 1px solid rgb(1, 7, 70);
    overflow: hidden;
    transition: all 100ms ease-in;
  }

  .game-container {
    display: flex;
    flex-direction: column;
    margin: 0rem 0rem 5rem 0rem;
  }

  .button-container {
    display: flex;
    flex-direction: row-reverse;
    margin: 0rem 1rem;
    float: left;
    transition: all 200ms ease;
  }

  #reset-label {
    color: #1324b8;
    display: flex;
    flex-direction: row;
    font-size: 0.75rem;
    align-items: center;
    font-weight: 600;
    opacity: 1;
    transition: all 500ms ease;
  }
}

